import Layout from "@components/layout";
import Seo from "@components/seo";
import React, { Fragment, useEffect, useState } from "react";
import BlogsDetails from "@screens/blog-details";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import BreadCrumb from "@components/common/breadcrumb";
import { utils } from "@core/helper";
import { useLocation } from "@reach/router";

const BlogDetailsScreen = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const location = useLocation();
  const slugUrl = location.pathname.split("/")?.[2];

  const [metaInfo, setMetaInfo] = useState(null);
  const [isMounted, setIsMounted] = useState(false);

  
  const loadMetaInfo = async () => {
    try {
      if (slugUrl) {
        console.log("slugUrl::", slugUrl);
        const metaDetails = await utils.getDocDataFilter(
          db,
          "meta",
          "slug",
          slugUrl
        );
        console.log("metaDetails::", metaDetails);
        if (metaDetails) {
          setMetaInfo(metaDetails?.[0]);
        }
      }
    } catch (err) {
      console.log("err:;", err);
    }
  };

  useEffect(() => {
    if (isMounted) {
      loadMetaInfo();
    }
  }, [isMounted, slugUrl]);

  useEffect(() => {
    setIsMounted(true);
  }, []);

  return (
    <Fragment>
      <BlogsDetails
        blogImages={props?.data?.allImageSharp?.edges}
        db={db}
        siteUrl={props?.data?.site?.siteMetadata?.siteUrl}
        metaInfo={metaInfo}
      />
    </Fragment>
  );
};

